@font-face {
  font-family: "EraserRegular";
  src: url('/font/eraser/EraserRegular.ttf') format("truetype");
}

    html {
      background: #f5f5f5;
    }
    .choice {
    transform: scale(1.8);
    -webkit-transition-duration: 1s;
    -moz-transition-duration: 1s;
    -o-transition-duration: 1s;
    transition-duration: 1s;
     z-index: 3;
 }
    .carousel{
      height: 400px;
      width:  100%;
    }
    .ribbon {
        padding-top: 20px;
        width: 200px;
        position: absolute;
        text-align: center;
        font-size: 12px!important;
        background: #e8c048;
        background: -webkit-gradient(linear, left top, left bottom, from(#e8c048), to(#91752f));
        background: -webkit-linear-gradient(top, #e8c048, #91752f);
        background: -moz-linear-gradient(top, #e8c048, #91752f);
        background: -ms-linear-gradient(top, #e8c048, #91752f);
        background: -o-linear-gradient(top, #e8c048, #91752f);
        background-image: -ms-linear-gradient(top, #e8c048 0%, #91752f 100%);
        -webkit-box-shadow: rgba(000,000,000,0.3) 0 1px 1px;
        -moz-box-shadow: rgba(000,000,000,0.3) 0 1px 1px;
        box-shadow: rgba(000,000,000,0.3) 0 1px 1px;

   }
.ribbon h1 {
   font-size: 18px!important;
   color: rgba(0, 0, 0, 1);
   /*color: rgba(28, 126, 217, 0.7);*/
   /*text-shadow: #c7a052 0 1px 0;*/
   margin:0px;
   padding: 10px 10px;
   /*font-family: 'Indie Flower', 'Helvetica Neue',Helvetica, sans-serif;*/
   font-family: "EraserRegular", "Comic Sans MS", sans-serif;
