   #clockdiv{
    font-family: sans-serif;
    color: #614d1f;
    text-shadow: #c7a052 0 1px 0;
    display: inline-block;
    font-weight: 100;
    text-align: center;
    font-size: 25px;
}

#clockdiv > div{
    padding: 10px;
    border-radius: 3px;
    background: #c7a217;
    display: inline-block;
}

#clockdiv div > span{
    padding: 10px;
    border-radius: 3px;
    background: #a27511;
    display: inline-block;
}
#start{
   background: transparent;
    font-size:0;
}
#sw_start, #sw_stop {
    color: #c7a217;
}



